import axios from 'axios';

import { USER_LOGIN, FETCH_EVENT } from '../TYPES';

export const adminLogin = password => async dispatch => {
  // const res = await axios.post('/api/admin/login', password); // FIXME: use real api
  const res = await axios.post('http://localhost:8080/api/login', { password });

  dispatch({ type: USER_LOGIN, payload: res.data });
}

export const updateEventList = events => dispatch => {
  // const res = await axios.put('/api/admin/event/list', events); // FIXME: use real api
  const res = axios.put('http://localhost:8080/api/admin/event/list', events);
  dispatch({ type: FETCH_EVENT, payload: events });
  // dispatch({ type: 'NONE' });
}

export const updateEventDetail = event => async dispatch => {
  var res = null;
  if (event.fileChanged) {
    var fileData = new FormData();
    fileData.append('newImage', event.file);
    // res = await axios.post('/api/admin/event/detail/image', fileData); // FIXME: use real api
    res = await axios.put(`http://localhost:8080/api/admin/event/detail/image/${event.id}`, fileData); // FIXME: use real api
  }
  // res = await axios.post('/api/admin/event/detail', event); // FIXME: use real api
  res = await axios.put(`http://localhost:8080/api/admin/event/detail/${event.id}`, event); // FIXME: use real api
  dispatch({ type: FETCH_EVENT, payload: res.data });
}

export const addNewEvent = event => async dispatch => {
  console.log(event);
  var res = null;
  var fileData = new FormData();
  fileData.append('newImage', event.file);
  res = await axios.post('http://localhost:8080/api/admin/event/new', event);
  res = await axios.put(`http://localhost:8080/api/admin/event/detail/image/${res.data}`, fileData);
  dispatch({ type: FETCH_EVENT, payload: res.data });
}